// FAQ section

.faq {
    &_media {
        border-radius: 16px;
        overflow: hidden;
        margin-top: 40px;
    }
    &_main {
        &-header {
            text-align: center;
            margin-bottom: 30px;
            &_title {
                margin-bottom: 15px;
            }
        }
        .accordion {
            &_component {
                max-width: 100%;
            }
        }
    }
}

// media queries

@media screen and (min-width: 991.98px) {
    .faq {
        &_main {
            &-header {
                text-align: left;
            }
        }
    }
}

@media screen and (min-width: 1199.98px) {
    .faq {
        &_media {
            margin-top: 0;
            height: 654px;
            img {
                height: 100%;
            }
        }
        &_main {
            margin-left: 80px;
        }
    }
}